import React from 'react';
import { Dropdown, Navbar, Nav, Container } from "react-bootstrap";

const Menu = () => {
    return (
        <Navbar bg="dark" variant="dark">
            <Container>
            <Navbar.Brand href="/">Магазин</Navbar.Brand>
                <Nav className="me-auto">
                    <Dropdown class="mx-3">
                        <Dropdown.Toggle id="dropdown-basic">
                            Категорії
                        </Dropdown.Toggle>
                        <Dropdown.Menu>
                            <Dropdown.Item href="/goods-category-1">Heritage Barkeep</Dropdown.Item>
                            <Dropdown.Item href="/goods-category-2">Rancher Carbine</Dropdown.Item>
                            <Dropdown.Divider />
                            <Dropdown.Item href="/goods-category-3">16" Rough Rider</Dropdown.Item>
                            <Dropdown.Item href="/goods-category-4">6-зарядний Rough Rider</Dropdown.Item>
                            <Dropdown.Item href="/goods-category-5">9-зарядний Rough Rider</Dropdown.Item>
                            <Dropdown.Item href="/goods-category-6">Bird Head Rough Rider</Dropdown.Item>
                            <Dropdown.Item href="/goods-category-7">Двокольоровий Rough Rider</Dropdown.Item>
                        </Dropdown.Menu>
                    </Dropdown>
                </Nav>
                <Navbar.Collapse className="justify-content-end">
                    <Nav.Link href="/basket"><img src="./images/shopping-cart.png"/></Nav.Link>
                    <Navbar.Text>
                        Signed in as: <a href="#login">Ruslan Percovytch</a>
                    </Navbar.Text>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default Menu;