import React from 'react';
import { Button, Card } from "react-bootstrap";

const CardItemComponent = (props) => {

    const addItem = () => {
        props.addItem(props.card);
    }

    const removeItem = () => {
        props.removeItem(props.card);
    }

    return (
        <div className="mt-3 col-12 col-sm-6 col-md-4 col-xl-3">
            <Card>
                <Card.Img variant="top" src={props.card.imgurl} height="200px"/>
                <Card.Body>
                    <Card.Title>{props.card.name}</Card.Title>
                    <Card.Text>
                        <strong>${props.card.price}</strong>
                    </Card.Text>
                    <div className="row">
                        <div className="col-3">
                            <div className="d-grid d-block">
                                <Button variant="success" onClick={addItem}>+</Button>
                            </div>
                        </div>
                        <div className="col-6">
                            <div className="d-grid d-block">
                                <Button variant="info"> {props.totalItems.length} шт.</Button>
                            </div>
                        </div>
                        <div className="col-3">
                            <div className="d-grid d-block">
                                <Button variant="danger" onClick={removeItem}>-</Button>
                            </div>
                        </div>
                    </div>
                </Card.Body>
            </Card>
        </div>
    );
};

export default CardItemComponent;
