import React, { useState } from 'react';
import { LOCALSTORE_TOTALITEMS } from "../../models/constants";
import CardItemComponent from "../../components/card-item-component/carditem-component";

const BasketComponent = () => {
    const itemsList = [
        {id: 1, name: "Манарин (шт.)", imgurl: "./images/food/nadarcott.jpg", price: 12},
        {id: 2, name: "Картопля (кг.)", imgurl: "./images/food/potato.jpg", price: 45},
        {id: 3, name: "Огірок (кг.)", imgurl: "./images/food/cucumber.jpg", price: 15},
        {id: 4, name: "Апельсин (шт.)", imgurl: "./images/food/orange.jpg", price: 30},
    ];

    const [itemsToSell, setItemsToSell] = useState(itemsList);
    const [totalItems, setTotalItems] = useState(JSON.parse(window.localStorage.getItem(LOCALSTORE_TOTALITEMS)));

    var i = [0];

    const getLocalStore = () => {
        let cardsLocal = window.localStorage.getItem(LOCALSTORE_TOTALITEMS);
        cardsLocal = cardsLocal ? JSON.parse(cardsLocal) : "Немає вибраних товарів";
        return (cardsLocal);
    };

    const addItem = (cardItem) => {
        setTotalItems([...totalItems, cardItem]);
        window.localStorage.setItem(LOCALSTORE_TOTALITEMS, JSON.stringify(totalItems));
    };

    const removeItem = (cardItem) => {
        let foundItemIndex;
        totalItems.forEach((item, index) => {
            if (item.id === cardItem.id) {
                foundItemIndex = index;
            }
        });
        totalItems.splice(foundItemIndex, 1);
        setTotalItems([...totalItems]);
        window.localStorage.setItem(LOCALSTORE_TOTALITEMS, JSON.stringify(totalItems));
    };

    return (
        
        <div className="container">
            <div className="row">
                <div align="center" class="py-2">
                    <ul class="list-group col-4 text-center">
                        <li class="list-group-item bg-primary text-white">Список вибраних товарів</li>
                        <li class="list-group-item bg-secondary text-white"><b>Всього вибрано:</b> {getLocalStore().length} товарів ціною ${getLocalStore().reduce((accumulator, item) => {return accumulator += item.price;}, 0)}.</li>
                    </ul>
                </div>
            </div>
            <div className="row">
                {Array.isArray(getLocalStore()) && getLocalStore().map((item) => {
                    if (i.indexOf(item.id) <= -1) {
                        i.push(item.id);
                        return (
                            <CardItemComponent key={item.index} card={item} addItem={addItem} removeItem={removeItem}
                                totalItems={getLocalStore().filter(itemFilter => itemFilter.id === item.id)} />
                        );
                    }
                })}
            </div>
        </div>
    );
};

export default BasketComponent;
