import React from 'react';
import { Button, Card } from "react-bootstrap";

const CardCategoryComponent = (props) => {

    return (
        <div class="mt-3 col-12 col-sm-6 col-md-4 col-xl-3">
            <Card class="h-100">
                <Card.Img variant="top" src={props.card.imgurl} />
                <Card.Body>
                    <Card.Title>{props.card.name}</Card.Title>
                    <Card.Text>
                        {props.card.description}
                    </Card.Text>
                    <Button variant="outline-danger" href={props.card.category}>Переглянути категорію</Button>
                </Card.Body>
            </Card>
        </div>
    );
};

export default CardCategoryComponent;
